body {
  background: $body-bg;
  color: $black;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'WarnockPro';
  transition: all 1s ease;
  // overflow-x: hidden;
}

.page {
	width: 100%;
	height: 100%;
}

.hidden {
  display: none;
}

.flex {
	display: flex;
}

section {
	display: flex;
	justify-content: center;
	width: 100%;
	min-height: 100vh;
	align-items: center;

	.container {
		margin: 70px 0 100px 0;
	}
}

.container {
	max-width: 950px;
	width: 100%;
	height: 100%;
	margin: 70px 0 25px 0;
	user-select: none;
	padding: 0 10px;
}

.container > div {
	flex-flow: column nowrap;
}

.content {
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: center;
}

button {
	background: none;
	border: 1px solid $gold;
	border-radius: 6px;
	padding: 0 15px;
	color: $white;
}

button span {
	line-height: 40px;
}

.gold-text {
	color: $gold;
}

#header {
	background:linear-gradient(180deg, rgba(45, 50, 64, 0.5) 0%, rgba(45, 50, 64, 0.9) 100%),  url(../img/header-bg.png) center center no-repeat,;
	background-size: cover;
}

#service {
	background: url(../img/service-bg.png) -330px 350px no-repeat;
	background-size: 100%;
}

#price {
	background: url(../img/price-bg.png) center center no-repeat;
	background-size: cover;
}

#faq {
	background: url(../img/faq-bg.png) right center no-repeat;
    background-size: 70%;
}

#feedback {
	background: url(../img/feedback-bg.png) center center no-repeat;
	background-size: cover;
}

#contacts {
	background: url(../img/Bitmap.jpg) center center no-repeat;
    background-size: cover;
}

h1 {
	font-size: $font-size--h1;
	margin: 1.5em 0;
	text-align: center;
}

h2 {
	font-size: $font-size--h2;
	margin: 1.3em 0;
	text-align: center;
}

h3 {
	font-size: $font-size--h3;
	margin: 1em 0;
}

h4 {
	font-size: $font-size--h4;
	margin: 1em 0;
}

h5,
h6 {
	font-size: $font-size--h5;
	margin: 1em 0;
}

@media (max-width: 768px) {

	h1 {
		font-size: 34px;
	}

	section {

		.container {
			margin-top: 30px;
		}
	}
}

@media (max-width: 480px) {

	#service {

		.container {
			margin-top: 30px;
		}
	}

	button {
		padding: 0 10px;

		span {
			line-height: 35px;
			font-size: $fz-text - 2px;
		}
	}
}
