// В этом файле должны быть стили для БЭМ-блока feedback, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.feedback {
	color: $white;

	&__slider {
		max-width: 600px;
		width: 100%;
	}
}

.slider {

	&__item {
		flex-flow: column nowrap;
		justify-content: center;
		align-items: center;

		&:focus {
			outline: none;
		}

		img {
			width: 115px;
			height: 115px;
			border-radius: 50%;
		    margin: 0 auto;
		    display: block;
		}

		p {
			text-align: center;
		}

		&-header {
			flex-flow: column nowrap;
			justify-content: center;
			align-items: center;
			margin-bottom: 30px;

			span {
				display: block;
				text-align: center;

				&.gold-text {
					font-size: $font-size--h2;
					line-height: 30px;
					margin-top: 20px;
				}

				&.slider__header-post {
					line-height: 34px;
				}
			}
		}
	}
}

@media (max-width: 768px) {

	.feedback {

		&__slider {
			max-width: 400px;
		}
	}
}

@media (max-width: 480px) {

	.feedback {

		&__slider {
			max-width: 280px;
		}
	}
}
