// В этом файле должны быть стили для БЭМ-блока service, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.service {

	&__description {

		p {
			text-align: center;
			line-height: 24px;
		}
	}

	&__lists {
		width: 100%;
		justify-content: space-around;

		&-block {
			flex-basis: 30%;
			flex-flow: column nowrap;
		}
	}

	&__list {

		li {
			padding-left: 30px;
			line-height: 34px;
		}

		&--before {
			position: relative;

			&::before {
				content: '';
				position: absolute;
				display: block;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				width: 15px;
				height: 13px;
				background: url(../img/burger.svg) center center no-repeat;
				background-size: cover;
			}
		}
	}

	button {
		color: $black;
	}
}

.button-consultation {
	margin-top: 70px;
}

@media (max-width: 480px) {

	.service {

		&__lists {
			flex-direction: column;
		}

		&__list {
			display: flex;
			flex-flow: column nowrap;
			align-items: center;

			li {
				padding: 0;
			}

			&--before::before {
				content: '';
				left: -20px;
				top: 43%;
			}
		}
	}
}
