// В этом файле должны быть стили для БЭМ-блока main-screen, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.main-screen {

	&__content {
		margin-top: 80px;

		.content__promo {
			flex-flow: column nowrap;
			align-items: center;
			justify-content: center;

			span {
				color: $white;
				text-transform: uppercase;

				&:first-child{
					font-size: 65px;
					line-height: 78px;
				}

				&:nth-child(2) {
					font-size: 53px;
					line-height: 62px;
					color: $gold;
				}

				&:nth-child(3) {
					font-size: 70px;
				}

				&:last-child {
					line-height: 23px;
				    text-transform: inherit;
				}
			}
		}

		.content__button {
			margin: 40px 0 65px 0;
		}

		.content__advantages {
			color: $white;
			flex-flow: row nowrap;

			.advantages__block {
				padding: 0 10px 0 30px;
				position: relative;

				&::before {
					content: '';
					display: block;
					position: absolute;
					left: 0;
					top: 50%;
					transform: translateY(-50%);
					width: 20px;
					height: 34px;
					background: url(../img/paragraph.svg) center center no-repeat;
					background-size: cover;
				}

				span {
					font-style: italic;
				}
			}

		}
	}
}

@media (max-width: 850px) {

	.main-screen {

		&__content {

			.content__advantages {
				flex-flow: row wrap;
				justify-content: space-around;

				.advantages__block {
					flex-basis: 45%;
					margin-top: 20px;
				}
			}
		}
	}
}

@media (max-width: 480px) {

	.main-screen {

		&__content {
			margin-top: 40px;

			.content__promo {

				span {

					&:first-child{
						font-size: 45px;
						line-height: 55px;
					}

					&:nth-child(2) {
						font-size: 37px;
						line-height: 45px;
					}

					&:nth-child(3) {
						font-size: 48px;
					}

					&:last-child {
						line-height: 20px;
					}
				}
			}

			.content__advantages {
				flex-flow: column nowrap !important;
			}
		}
	}
}
