// В этом файле должны быть стили для БЭМ-блока header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.header {
	align-items: center;
    width: 100%;
    justify-content: space-between;

    &__logo {
    	display: block;
    	width: 80px;
    	height: 100px;
    	background: url('../img/logo.svg') center center no-repeat;
    	background-size: 100%;
    }

	&__menu {
		margin-left: 112px;
	}

	&__contacts {
		flex-direction: column;
		align-items: flex-end;
		color: $white;

		&-block {
			flex-flow: column nowrap;
			align-items: flex-end;

			span {
				display: inline-block;
				font-size: $fz-text - 2;
				margin: 5px 0 15px 0;
			}
		}

		a {
			font-size: $fz-text + 2;
		}


		button {
			border: 1px solid $white;
			color: $white;
		}
	}
}

.menu__list {

	&-item {
		padding-left: 15px;
		list-style-type: none !important;
		position: relative;

		&:hover,
		&:active,
		&:focus {

			&:first-child {

				a::before {
					content: '';
					display: block;
					width: 100%;
					height: 1px;
					background-color: $white;
					position: absolute;
					bottom: -4px;
					right: 0;
				}
			}

			a::before {
				content: '';
				display: block;
				width: calc(100% - 15px);
				height: 1px;
				background-color: $white;
				position: absolute;
				bottom: -4px;
				right: 0;
			}
		}

		&.active {
			position: relative;

			a {

				&::before {
					content: '';
					display: block;
					width: calc(100% - 15px);
					height: 1px;
					background-color: $white;
					position: absolute;
					bottom: -4px;
					right: 0;

				}
			}
		}

		&:first-child {
			padding: 0;
		}

		a {
			color: $white;
		}
	}
}

@media (max-width: 850px) {

	.header {

		&__menu {
			margin-left: inherit;
		}

		&__contacts {
		}
	}
}

@media (max-width: 768px) {

	.header {
		flex-direction: column;

		&__menu {
		    margin: 20px 0;
		}

		&__contacts {

			span {
				margin: 5px 0 0 0;
			}

			button {
				display: none;
			}

			&-block {
				align-items: center;
			}
		}
	}
}

@media (max-width: 480px) {

	.menu__list-item {
		padding-left: 10px;

		&:first-child {
			padding: 0;
		}

		&.active {

			a::before {
				content: '';
				width: calc(100% - 10px);
				bottom: -1px;
			}
		}

		&:hover,
		&:active,
		&:focus {

			a::before {
				content: '';
				width: calc(100% - 10px);
				bottom: -1px;
			}
		}

		a {
			font-size: $fz-text - 2px;
		}
	}

	#header {

		.header {

			&__menu {
				order: 1;
			}

			&__contacts {
				order: 2;
			}

			&__logo {
				order: 3;
				margin-top: 40px;
			}
		}
	}

	section:not(#header) {

		.header {
			flex-flow: row wrap;
			justify-content: flex-start;

			&__menu {
				order: 3;
				flex-basis: 100%;

				.menu__list {
					justify-content: center;
					border-bottom: 1px solid #DADADA;

					li {
						line-height: 25px;
					}
				}
			}

			&__contacts {
				order: 2;
			}

			&__logo {
				order: 1;
				margin: 0;
				width: 40px;
				height: 50px;
			}

			&__contacts {
				flex-flow: row wrap;
				margin-left: 20px;
			    width: calc(100% - 80px);
			    justify-content: center;


				&-block {
					color: #979797;
				}
			}
		}
	}
}
