// В этом файле должны быть стили для БЭМ-блока header-white, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.header-white {

	.header__contacts {
		color: $black;

		button {
			border: 1px solid $black;
			color: $black;
		}
	}

	.menu__list-item {

		a {
			color: $black;

		}

		&:hover,
		&:active,
		&:focus {

			a::before {
				content: '';
				background-color: $black;
			}
		}

		&.active {

			a {

				&::before {
					content: '';
					background-color: $black;
				}
			}
		}
	}

	.header__logo {
    	background: url('../img/logo-white.svg') center center no-repeat;
    	background-size: 100%;
	}
}

@media (max-width: 480px) {

	.menu__list-item {

	}
}
