// В этом файле должны быть стили для БЭМ-блока contacts, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.contacts {

	.contacts__map {
		border-radius: 10px;
		overflow: hidden;
		box-shadow: 0px 23px 110px rgba(60, 57, 80, 0.179914);
	}

	#map {
		background-color: $gray;
		width: 421px;
		height: 329px;
	}

	&__address {
		flex-flow: column nowrap;
		margin-left: 30px;

		.address__tel {

			a {
				display: block;
				margin-bottom: 10px;
			}
		}
	}
}

@media (max-width: 768px) {

	.contacts {

		&__block {
			flex-direction: column;
		}

		&__map {
			width: 250px;
			height: 250px;
		}

		&__address {
			margin: 0;
		}
	}
}
