// В этом файле должны быть стили для БЭМ-блока article, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.article {

	&__wrap.vertical {
		flex-flow: column nowrap;

		.article__text-block {
			width: 100%;
		}

		.article__right-block {
		    max-width: none;

			&-link {
				flex-flow: row nowrap;
			    justify-content: space-between;

				a {
					max-width: 300px;
					width: 100%;
				}
			}
		}
	}

    p {
    	line-height: 20px;
    }

    ul:not(.menu__list) {
    	margin: 10px 0 10px 20px;

    	li {
    		list-style-type: disc;
    		line-height: 24px;
    	}
    }

	&__text-block {
		flex-flow: column nowrap;
		padding-right: 15px;
	    width: calc(100% - 300px);


		.text-block__answer {
			flex-flow: column nowrap;

			a {
				padding: 5px 0 5px 15px;
				text-decoration: underline;
				position: relative;

				&::before {
					content: '§';
					position: absolute;
					left: 0;
				}
			}
		}
	}

	&__right-block {
	    max-width: 300px;
	    width: 100%;
	    padding-top: 13px;
	    flex-flow: column nowrap;

		&-number {
			flex-flow: column nowrap;
			margin-bottom: 10px;

			div {
				display: flex;
				flex-flow: row nowrap;
				align-items: center;

				span {

					&:first-child{
						font-size: 56px;
					}

					&:last-child {
						padding-left: 10px;
					}
				}
			}
		}

		&-link {
			flex-flow: column nowrap;

			a {
				display: flex;
				justify-content: flex-end;
				flex-flow: column nowrap;
				width: 100%;
				background-color: #3C3950;
				height: 160px;
				color: #fff;
				margin-top: 15px;
				border-radius: 10px;
				position: relative;

				img {
					width: 100%;
					height: 100%;
					opacity: 0.5;
					position: absolute;
					border-radius: 10px;
					top: 0;
					left: 0;
				}

				span {
					display: inline-block;
					padding-left: 10px;
					z-index: 2;

					&:first-child {
						font-size: 22px;
						line-height: 28px;
					}

					&:last-child {
						padding-bottom: 25px;
						font-size: 14px;
					}
				}
			}
		}
	}
}

@media (max-width: 768px) {

	.article {

		&__text {

			&-block {
				width: 100%;
			}
		}

		&__right {

			&-block {
				display: none;
			}
		}
	}
}
