// В этом файле должны быть стили для БЭМ-блока faq, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.faq {

	&__list {
		width: 100%;
		text-align: left;

		li {
			line-height: 40px;

			a {
				color: black;
				padding-left: 40px;
				position: relative;

				&::before {
					position: absolute;
					content: '';
					display: block;
					background: url(../img/paragraph.svg) center center no-repeat;
					background-size: 100%;
				    width: 15px;
				    height: 25px;
				    top: 50%;
				    left: 10px;
					transform: translateY(-50%);
				}

				&:hover {
					text-decoration: underline;
				}
			}
		}
	}
}

@media (max-width: 480px) {

	.faq {

		&__list {

			li {
				line-height: 26px;
				margin-bottom: 10px;

				a {
					display: block;
					font-size: $fz-text - 2px;
				}
			}
		}
	}
}
