// В этом файле должны быть стили для БЭМ-блока price, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.price {
	color: $white;

	&__table {
		flex-flow: column nowrap;
		width: 100%;
		margin-bottom: 20px;

		> div {
			display: flex;
			flex-flow: row nowrap;

			> div {

				&:first-child {
					flex-basis: 8%;
					text-align: center;
				}

				&:nth-child(2) {
					flex-basis: 83%;
					padding-right: 10px;
				}

				&:last-child {
					flex-basis: 15%;
					padding-right: 15px;
					text-align: right;
				}
			}
		}

		&-title {
			line-height: 40px;
		}

		&-row {
			color: $black;
			background-color: $white;
			line-height: 40px;

			&:nth-child(2n-1) {
				background-color: #F6F6F6;
			}

			&:nth-child(2) {
				border-radius: 5px 5px 0 0;
			}

			&:last-child {
				border-radius: 0 0 5px 5px;
			}

			&-question {
				cursor: pointer;
				padding: 0 9px;
				border: 1px solid $black;
				border-radius: 50%;
				display: inline;
				position: relative;
			}

			&--sup {
				display: none;
			}

			&-question:hover &--sup {
				display: block;
			    white-space: nowrap;
			    cursor: default;
				position: absolute;
				top: -75%;
				left: 50%;
				transform: translate(-50%, -50%);
				padding: 0 15px;
				line-height: 30px;
				background-color: rgba(0, 0, 0, 0.604);
				color: $white;
				border-radius: 5px;
			}

			&-question:hover &--sup::before {
				content: '';
				display: block;
				position: absolute;
			    bottom: -16px;
			    border: 7px solid transparent;
			    border-top: 9px solid rgba(0, 0, 0, 0.604);
			    left: 50%;
			    transform: translateX(-50%);
			}
		}
	}
}

@media (max-width: 480px) {

	.price {

		&__table {

			> div {

				> div {

					&:first-child {
						flex-basis: 10%;
					}

					&:last-child {
						flex-basis: 25%;
					}
				}
			}

			&-row {
				font-size: $fz-text - 2px;
			}
		}
	}
}
